import NuxtLinkButton from '@/components/Button/NuxtLinkButton'

export default {
    components: {
        NuxtLinkButton,
    },
    props: {
        data: {
            type: Object,
            default () {
                return {}
            },
        },
    },
    data () {
        return {
            isDesktop: null,
        }
    },
    mounted () {
        this.resize()
        window.addEventListener('resize', this.resize)
    },
    methods: {
        resize () {
            this.isDesktop = window.innerWidth >= 1200
        },
        capitalizeFirstLetter (string) {
            return string?.toLowerCase().charAt(0).toUpperCase() + string?.slice(1)
        },
    },
}
